.container {
  width: 100%;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open-Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open-Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open-Sans", "Arial", sans-serif;
  color: #000000;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.header {
  overflow: hidden;
}
@media (min-width: 768px) {
  .header {
    background-color: #f9f9f9;
  }
  .header--inner {
    background-color: #f1f1f1;
  }
}

@media (min-width: 768px) {
  .nav {
    display: grid;
    grid-template-columns: 1fr 1.15fr;
    grid-template-areas: "logo nav-user" "nav-site delivery";
  }
}
@media (min-width: 1150px) {
  .nav {
    grid-template-columns: 1fr 150px 1fr;
    grid-template-areas: "nav-site logo nav-user" "nav-desktop . delivery";
  }
}

@media (max-width: 768px) {
  .header__wrapper {
    padding: 0;
  }
}

.nav__container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 79px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .nav__container {
    grid-area: logo;
    border-bottom: none;
    z-index: 2;
  }
}

.nav__logo {
  width: 86px;
  height: 35px;
}
.nav__logo:hover {
  opacity: 0.6;
}
.nav__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__logo {
    width: 113px;
    height: 46px;
    margin-right: auto;
  }
}
@media (min-width: 1150px) {
  .nav__logo {
    width: 150px;
    height: 72px;
    margin: 0;
  }
}

.nav__button {
  position: absolute;
  top: 32px;
  right: 35px;
  border: none;
  width: 20px;
  height: 14px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
}
.nav__button:hover {
  cursor: pointer;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}

.nav--closed .nav__button {
  background-image: url("../img/svg/menu-open.svg");
}

.nav--opened .nav__button {
  background-image: url("../img/svg/menu-close.svg");
}

@media (max-width: 768px) {
  .nav--closed .nav__list {
    display: none;
  }

  .nav--opened .nav__list--site {
    display: block;
    position: absolute;
    z-index: 1;
    top: 79px;
    background-color: #ffffff;
    min-width: 320px;
  }

  .nav--opened .nav__list--user {
    display: block;
    position: absolute;
    z-index: 1;
    top: 237px;
    background-color: #ffffff;
    min-width: 320px;
  }

  .nav-nojs .nav__button {
    display: none;
  }

  .nav-nojs .nav__list--user {
    display: block;
  }

  .nav-nojs .nav__list--site {
    display: block;
  }
}
.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
}

@media (min-width: 768px) {
  .nav__list--user {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    grid-area: nav-user;
    justify-content: end;
    position: relative;
    z-index: 1;
  }
  .nav__list--user::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 200vw;
    background-color: #ffffff;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    border-bottom: 1px solid #ececec;
  }
}

@media (min-width: 768px) {
  .nav__list--site {
    display: flex;
    flex-wrap: wrap;
    gap: 36px;
    grid-area: nav-site;
    align-items: center;
  }
}
@media (min-width: 1150px) {
  .nav__list--site {
    z-index: 2;
    gap: 0;
  }
}

.nav__list--desktop {
  display: none;
}
@media (min-width: 1150px) {
  .nav__list--desktop {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 60px;
  }
}

.nav__item {
  padding: 27px 0 27px 30px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .nav__item {
    border-bottom: none;
    max-width: 250px;
  }
}

@media (min-width: 768px) {
  .nav__item--search {
    min-width: 84px;
    border-left: 1px solid #ececec;
    padding: 30px 31px;
  }
}

@media (min-width: 768px) {
  .nav__item--basket {
    max-width: 265px;
    border-left: 1px solid #ececec;
  }
}

.nav__item--site {
  padding-left: 76px;
}
@media (min-width: 768px) {
  .nav__item--site {
    padding: 21px 0;
  }
}
@media (min-width: 1150px) {
  .nav__item--site {
    padding: 29px 35px;
    border-right: 1px solid #ececec;
  }
  .nav__item--site:nth-child(odd) {
    padding-left: 0;
  }
  .nav__item--site:nth-child(even) {
    border-right: none;
  }
}

@media (min-width: 1150px) {
  .nav__item--desktop {
    padding: 21px 0;
  }
}

.nav__svg {
  margin-right: 24px;
  opacity: 0.2;
}

@media (min-width: 768px) {
  .nav__svg--search {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .nav__link-text {
    display: none;
  }
}

.nav__link {
  font-family: inherit;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.nav__link:hover span {
  opacity: 0.6;
}
.nav__link:hover .nav__svg {
  fill: #63d1bb;
  opacity: 1;
}
.nav__link:active {
  opacity: 0.3;
}
.nav__link:active .nav__svg {
  fill: #63d1bb;
  opacity: 0.3;
}

.nav__link-span {
  display: inline-block;
  max-width: 182px;
}

@media (min-width: 768px) {
  .nav__link--site {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .nav__link--site {
    font-size: 17px;
  }
}

@media (min-width: 1150px) {
  .nav__link--desktop {
    font-size: 15px;
  }
}

.nav__delivery-offer {
  color: #444444;
  padding: 21px 0;
  display: none;
}
@media (min-width: 768px) {
  .nav__delivery-offer {
    display: block;
    text-align: end;
    grid-area: delivery;
  }
}

.nav__list--closed {
  display: none;
}

